import { ajax } from 'rxjs/observable/dom/ajax';
import { Observable } from 'rxjs/Observable';
import { catchError } from 'rxjs/operators';
import history from '../history';

const baseURL = "http://corevoyant.com:9000";

const requestHeader = () => {
  const headers = {
    'Content-Type': 'application/json',
  };

  let token: string | boolean = false;

  if (localStorage.session && JSON.parse(localStorage.session).accessToken) {
    token = `Bearer ${JSON.parse(localStorage.session).accessToken}`;
  }
  if(token){
    headers['Authorization'] = token;
  }

  return headers;
};

const handleError = (operation: string) => (err: any) => {
  const errMsg = `Error in ${operation}()`;
  //console.log(`${errMsg}:`, err);

  if (err.status === 401) {
    localStorage.clear();
    history.push('/login');
  }

  if (err.status === 503) {
    //console.log(err.message);
    //console.log(err.request);
    //console.log(err.response);
    //console.log(err.responseType);
    //console.log(err.status);
  }

  return Observable.throw(errMsg);
};

export const auth = () =>
  ajax({
    headers: requestHeader(),
    method: 'GET',
    url: `${baseURL}/AdminLogin`,
    withCredentials: true,
  }).pipe(catchError(handleError('auth')));

export const login = (payload) =>
  ajax({
    headers: requestHeader(),
    method: 'POST',
    url: `${baseURL}/AdminLogin?username=${payload.username}&password=${payload.password}`,
  }).pipe(catchError(handleError('login')));

export const logout = () =>
  ajax({
    headers: requestHeader(),
    method: 'GET',
    url: `${baseURL}/auth/logout`,
    withCredentials: true,
  }).pipe(catchError(handleError('logout')));

export const resetPassword = (payload) =>
  ajax({
    headers: requestHeader(),
    method: 'POST',
    url: `${baseURL}/reset_password`,
    body: payload,
  }).pipe(catchError(handleError('resetPassword')));
